import { useMemo, useState } from "react";
import { yupResolver } from "@hookform/resolvers/yup";
import { useForm } from "react-hook-form";
import { object, string } from "yup";
import { useAppContext } from "context";
import { chat } from "services";
import { IMG_OBJ, SERVICE } from "constant";

export const useUserInfo = () => {
  const { setState, notiList } = useAppContext();
  const [loading, setLoading] = useState(false);

  const validationSchema = useMemo(
    () =>
      object().shape({
        program: string()
          .nullable()
          .trim()
          .required("Vui lng nhp tn chng trnh"),
        name: string()
          .nullable()
          .trim()
          .required("Vui lng nhp tn ngi dng"),
        habit: string()
          .nullable()
          .trim()
          .required("Vui lng nhp thi quen tiu dng/s thch"),
        content: string().nullable().trim().required("Vui lng nhp ni dung"),
        service: string().nullable().trim().required("Vui lng chn dch v"),
      }),
    []
  );
  const form = useForm<Data>({
    defaultValues,
    resolver: yupResolver(validationSchema) as any,
  });

  const onGenerate = form.handleSubmit((noti) => {
    form.reset({ ...noti, imgs: null as any }, { keepDirtyValues: true });
  });

  const onSubmit = form.handleSubmit(async (noti) => {
    const img = document.querySelector("#imgs .swiper-slide-active") as Element;
    const index = img.getAttribute("data-swiper-slide-index") as string;
    setLoading(true);
    await chat(
      `
      MoMo cn to ra nhng thng bo u i siu vui v, c tnh ku gi hnh ng siu cao v siu c nhn ha vi khch hng ca MoMo. Hy gip to ra 5 thng bo u i cho chng trnh A ca MoMo:
      Tn chng trnh u i A: ${noti.program}.
      M t v u i A: ${noti.content}.
      Tn dch v A ca MoMo: ${noti.service}.
      Tn ca i tng mc tiu A: ${noti.name}.
      Mt vi thi quen hoc s thch c o ca i tng mc tiu A: ${noti.habit}.
      
      Hy to 5 thng bo u i ngn v gn gi. Ni dung ch t 2 n 3 cu l ti a, vi s thay th t "MoMo" cho "Chng ti" hoc "Chng t".

      Hy to 5 thng bo u i c cu trc
      Thng bo n:
      Tiu :
      Ni dung: 
      `,
      (message) => {
        console.log(message);
        const random = Math.floor(Math.random() * 5);
        const messSplit = message.split("\n").filter((item) => item);
        console.log(messSplit);
        let notiTitle = messSplit[random * 3 + 1];
        notiTitle = notiTitle.substring(notiTitle.indexOf("Tiu :") + 8);
        let notiContent = messSplit[random * 3 + 2];
        notiContent = notiContent.substring(
          notiContent.indexOf("Ni dung:") + 9
        );
        setState({
          notiList: [
            { ...noti, notiTitle, notiContent, img: noti.imgs[Number(index)] },
            ...notiList,
          ],
        });
        setLoading(false);
        form.reset({ ...defaultValues });
      }
    );
  });
  return { form, onSubmit, loading, onGenerate };
};

const defaultValues = {
  program: "",
  name: "",
  habit: "",
  content: "",
  service: null as any,
  imgs: [],
};

// const defaultValues = {
//   program: "Gim gi",
//   name: "Ho",
//   habit: "Chi game",
//   content: "Gim 10k vi th np 100k",
//   service: SERVICE[0],
//   imgs: IMG_OBJ["V Tr Sau"],
// };

interface Data {
  program: string;
  name: string;
  habit: string;
  content: string;
  service: string;
  imgs: string[];
}
